/* 水波纹 */
/* .rippleMixin(20px, 50px, rgba(0, 0, 0, .2), 200, person); */
.catalogue {
  padding-left: 60px;
}
.catalogue .title {
  font-size: 16px;
  line-height: 32px;
  color: #aaaaaa;
  text-transform: uppercase;
}
.catalogue .sep {
  width: 100%;
  height: 1px;
  margin: 6px 0 10px;
  background-color: #dedede;
}
.catalogue ul {
  font-size: 0;
  max-height: 256px;
  overflow-y: auto;
}
.catalogue ul .ripple {
  cursor: pointer;
  display: inline-block;
  position: relative;
  padding: 0 0px;
  height: 32px;
}
.catalogue ul .rippleWrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
@-webkit-keyframes ripple {
to {
    -webkit-transform: scale(40);
            transform: scale(40);
    opacity: 0;
}
}
@keyframes ripple {
to {
    -webkit-transform: scale(40);
            transform: scale(40);
    opacity: 0;
}
}
.catalogue ul .rippleSpan {
  width: 10px;
  height: 10px;
  position: absolute;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 100%;
  -webkit-transform: scale(0);
          transform: scale(0);
  pointer-events: none;
  -webkit-animation: ripple 0.4s ease-out;
          animation: ripple 0.4s ease-out;
}
.catalogue ul::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
.catalogue ul::-webkit-scrollbar-track-piece {
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-border-radius: 6px;
}
.catalogue ul::-webkit-scrollbar-thumb:vertical {
  height: 5px;
  background-color: rgba(125, 125, 125, 0.7);
  -webkit-border-radius: 6px;
}
.catalogue ul::-webkit-scrollbar-thumb:horizontal {
  width: 5px;
  background-color: rgba(125, 125, 125, 0.7);
  -webkit-border-radius: 6px;
}
.catalogue ul li {
  width: 100%;
  font-size: 14px;
  line-height: 32px;
  color: #383838;
}
.catalogue ul li p {
  position: absolute;
  height: 32px;
  top: 0;
  left: 0;
  width: 100%;
  background: #4db6ac;
  opacity: 0;
  filter: alpha(opacity=0);
}
.catalogue ul li i {
  position: absolute;
  font-style: normal;
  font-family: sans-serif;
  padding: 0 5px 0 10px;
}
.catalogue ul li .info {
  position: absolute;
  left: 22px;
}
.catalogue ul li .number {
  position: absolute;
  top: 7px;
  right: 0;
  padding: 0 8px;
  height: 18px;
  line-height: 18px;
  font-size: 12px;
  background-color: #4db6ac;
  color: #fff;
  border-radius: 4px;
}
.catalogue ul li.active {
  background: #4db6ac;
}
.catalogue ul li.active i {
  color: #fff;
}
.catalogue ul li.active .info {
  color: #fff;
}
.catalogue .more {
  line-height: 30px;
  text-align: center;
  border-radius: 2px;
  border: 1px solid #dcdcdc;
  font-size: 14px;
  color: #787878;
  background-color: #f7f7f7;
  margin-top: 15px;
  cursor: pointer;
}



