/* 水波纹 */
/* .rippleMixin(20px, 50px, rgba(0, 0, 0, .2), 200, person); */
.article {
  width: 75%;
  float: left;
  position: relative;
}
.article .item {
  padding: 34px 20px 20px;
  border-bottom: 1px solid #dedede;
  transition: all .3s;
}
.article .item:hover {
  box-shadow: 0 5px 10px 2px rgba(0, 0, 0, 0.1);
}
.article .mainTitle {
  font-size: 20px;
  line-height: 34px;
  color: #666;
  cursor: pointer;
}
.article .mainTitle:hover {
  text-decoration: underline;
  color: #26a69a;
}
.article .mainTitle span {
  color: #26a69a;
}
.article .subTitle {
  font-size: 16px;
  line-height: 30px;
  color: #383838;
}
.article ul.tags {
  *zoom: 1;
  margin-top: 10px;
}
.article ul.tags:before,
.article ul.tags:after {
  content: "";
  display: table;
}
.article ul.tags:after {
  clear: both;
}
.article ul.tags li {
  float: left;
  font-size: 12px;
  padding: 5px 10px;
  margin-right: 10px;
  color: #fff;
  background-color: #00b5a3;
  border-radius: 6px;
  cursor: pointer;
  transition: all .5s;
}
.article ul.tags li:hover {
  background-color: #f76689;
}
.article .intro {
  font-family: 'sourcesanspro', 'Helvetica Neue', Arial, sans-serif;
  margin-top: 20px;
  font-size: 14px;
  color: #828282;
  line-height: 24px;
  height: 72px;
  overflow: hidden;
  cursor: pointer;
}
.article .intro:hover {
  text-decoration: underline;
}
.article .info {
  font-family: 'sourcesanspro', 'Helvetica Neue', Arial, sans-serif;
  margin-top: 30px;
  font-size: 14px;
  line-height: 20px;
  *zoom: 1;
}
.article .info:before,
.article .info:after {
  content: "";
  display: table;
}
.article .info:after {
  clear: both;
}
.article .info .date,
.article .info .column {
  float: left;
  font-size: 12px;
  color: #828282;
}
.article .info .more,
.article .info .delete {
  float: right;
  border-radius: 2px;
  color: #26a69a;
  cursor: pointer;
  margin-left: 8px;
}
.article .info .more:hover,
.article .info .delete:hover {
  color: #0d7b71;
}
.article .pagination {
  margin: 50px 0 80px;
  padding: 0 20px;
  *zoom: 1;
}
.article .pagination:before,
.article .pagination:after {
  content: "";
  display: table;
}
.article .pagination:after {
  clear: both;
}
.article .pagination button {
  background-color: transparent;
  color: #2e9c92;
  font-size: 14px;
  line-height: 30px;
  padding: 0 10px;
  border-radius: 2px;
  cursor: pointer;
  transition: all .3s;
}
.article .pagination .prev:hover,
.article .pagination .next:hover {
  color: #fff;
  background-color: #2e9c92;
}
.article .pagination .prev {
  float: left;
}
.article .pagination .next {
  float: right;
}
.article .pagination i {
  font-family: sans-serif;
  font-style: normal;
}



