/* 水波纹 */
/* .rippleMixin(20px, 50px, rgba(0, 0, 0, .2), 200, person); */
.header {
  position: fixed;
  z-index: 990;
  top: 0;
  left: 0;
  right: 0;
  background-color: #26a69a;
  height: 50px;
  color: #fff;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.header.BAM {
  left: 200px;
  right: 0;
  background-color: #fff;
  color: #2F4050;
}
.header h1 {
  margin-left: 20px;
  float: left;
  font-size: 20px;
  line-height: 50px;
  padding: 0 10px;
  width: 160px;
  cursor: pointer;
}
.header .nav {
  position: absolute;
  left: 50%;
  top: 0;
  margin-left: -500px;
}
.header .nav .ripple {
  cursor: pointer;
  display: inline-block;
  position: relative;
  padding: 0 20px;
  height: 50px;
}
.header .nav .rippleWrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
@-webkit-keyframes ripple {
to {
    -webkit-transform: scale(20);
            transform: scale(20);
    opacity: 0;
}
}
@keyframes ripple {
to {
    -webkit-transform: scale(20);
            transform: scale(20);
    opacity: 0;
}
}
.header .nav .rippleSpan {
  width: 10px;
  height: 10px;
  position: absolute;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 100%;
  -webkit-transform: scale(0);
          transform: scale(0);
  pointer-events: none;
  -webkit-animation: ripple 0.4s ease-out;
          animation: ripple 0.4s ease-out;
}
.header .nav ul {
  *zoom: 1;
}
.header .nav ul:before,
.header .nav ul:after {
  content: "";
  display: table;
}
.header .nav ul:after {
  clear: both;
}
.header .nav ul li {
  float: left;
  line-height: 50px;
}
.header .nav ul li.activeType,
.header .nav ul li:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.header .nav ul li i {
  display: inline-block;
  margin-right: 3px;
  font-size: 16px;
  line-height: 21px;
  vertical-align: middle;
}
.header .headerR {
  float: right;
  margin-right: 20px;
}
.header .headerR .search {
  float: left;
  width: 44px;
  height: 50px;
  overflow: hidden;
  background-color: #26a69a;
  font-size: 0;
  padding: 12px 10px 0 10px;
  box-sizing: border-box;
  transition: all .5s;
}
.header .headerR .search i {
  float: left;
  line-height: 26px;
  font-size: 18px;
}
.header .headerR .search :-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #fff;
  opacity: .5;
}
.header .headerR .search ::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #fff;
  opacity: .5;
}
.header .headerR .search input:-ms-input-placeholder,
.header .headerR .search textarea:-ms-input-placeholder {
  color: #fff;
  opacity: .5;
}
.header .headerR .search input::-webkit-input-placeholder,
.header .headerR .search textarea::-webkit-input-placeholder {
  color: #fff;
  opacity: .5;
}
.header .headerR .search input {
  float: right;
  width: 0;
  border: 0;
  font-size: 14px;
  height: 26px;
  box-sizing: border-box;
  color: #fff;
  background-color: transparent;
  transition: all .5s;
}
.header .headerR .search.active {
  width: 200px;
  background-color: #009688;
}
.header .headerR .search.active input {
  width: 155px;
  padding-left: 10px;
}
.header .headerR .person {
  float: right;
  height: 50%;
  cursor: pointer;
  position: relative;
  transition: all 1s;
}
.header .headerR .person .Tooltip {
  display: none;
}
.header .headerR .person:hover .Tooltip {
  padding: 5px 8px;
  position: absolute;
  bottom: -45px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 70px;
  line-height: 24px;
  text-align: center;
  background-color: #1F2D3D;
  color: #fff;
  border-radius: 4px;
}
.header .headerR .person:hover .Tooltip:before {
  content: '';
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: -7px;
  width: 0;
  height: 0;
  border-top: 0 solid transparent;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 7px solid #1F2D3D;
}
.header .headerR .person:hover .Tooltip {
  display: block;
}
.header .headerR .person .info {
  width: 50px;
  height: 50px;
  padding: 8px;
  display: inline-block;
  box-sizing: border-box;
  vertical-align: top;
}
.header .headerR .person .info .avatar {
  overflow: hidden;
}
.header .headerR .person .info img {
  height: 34px;
}
.header .headerR .person.activeType,
.header .headerR .person:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.header .headerR .ripple {
  cursor: pointer;
  display: inline-block;
  position: relative;
  padding: 0 0;
  height: 50px;
}
.header .headerR .rippleWrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
@keyframes ripple {
to {
    -webkit-transform: scale(20);
            transform: scale(20);
    opacity: 0;
}
}
.header .headerR .rippleSpan {
  width: 10px;
  height: 10px;
  position: absolute;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 100%;
  -webkit-transform: scale(0);
          transform: scale(0);
  pointer-events: none;
  -webkit-animation: ripple 0.4s ease-out;
          animation: ripple 0.4s ease-out;
}
.header .headerR .logout {
  float: left;
  width: 50px;
  height: 50px;
  text-align: center;
  cursor: pointer;
}
.header .headerR .logout .Tooltip {
  display: none;
}
.header .headerR .logout:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.header .headerR .logout:hover .Tooltip {
  display: block;
}
.header .headerR .logout .iconfont {
  font-size: 18px;
  line-height: 50px;
}
.header .headerR .logout .Tooltip {
  padding: 5px 8px;
  position: absolute;
  bottom: -45px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 50px;
  line-height: 24px;
  text-align: center;
  background-color: #1F2D3D;
  color: #fff;
  border-radius: 4px;
}
.header .headerR .logout .Tooltip:before {
  content: '';
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: -7px;
  width: 0;
  height: 0;
  border-top: 0 solid transparent;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 7px solid #1F2D3D;
}



